import { Injectable } from "@angular/core";
@Injectable()
export class UtilityService{
    islogged(): Promise <boolean>{
    if (typeof (Storage) !== 'undefined'){
        if (sessionStorage.getItem('User')){
            return Promise.resolve(true);
        }
    }
    return Promise.resolve(false);
    }
}



